.friendList {
  margin: 0 auto;
  width: 330px;
  padding-left: 0;
}

.item {
  padding: 15px 15px;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
    1px 4px 6px rgba(0, 0, 0, 0.16);
  border-radius: 3px;
  background-color: #ffffff;
  list-style: none;
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.item:hover {
  transform: scale(1.1);
}

.item:not(:last-child) {
  margin-bottom: 20px;
}

.status {
  content: '';
  width: 20px;
  height: 20px;
  border-radius: 50px;
  margin-right: 10px;
}

.avatar {
  width: 70px;
  margin-right: 10px;
}

.name {
  font-size: 24px;
  font-weight: 500;
  line-height: 1.62;
  letter-spacing: 0.01em;
}
